import {useState} from 'react'
import { UpOutlined,DownOutlined } from '@ant-design/icons';
import './statistics.css'
function Statistics(){
    const barmain=[
        {
            id:0,
            num:0,
            task:''
        },
        {
            id:1,
            num:0,
            task:''
        },
        {
            id:2,
            num:0,
            task:''
        },
        {
            id:3,
            num:0,
            task:''
        }
    ]
    const select =[
        {
            id:0,
            title:""
        },
        {
            id:1,
            title:"wsertyuio"
        }
    ]
    const [input_select,setinput_select] = useState('')
    const header_wrapper =[
        {id:0,header:''},
        {id:1,header:''},
        {id:2,header:''},
        {id:3,header:''},
        {id:4,header:''},
        {id:5,header:''},
    ]

    const [project_body_select,setproject_body_select] = useState(0)
    function projectBodySelect(){
        if(project_body_select===0){
            setproject_body_select(1)
        }else{
            setproject_body_select(0)
        }
    }
    function statisticsSection(index){
        setinput_select(select[index].title)
        console.log(select[index].title);
        
        setproject_body_select(0)
        // console.log('ok');
        
    }
    return(
        <div className="statistics_box">
            <h3></h3>
            <div className="statistics_content_box">
                <div className="statistics_section project-body">
                    <h6><span></span></h6>
                    <div className="vux-flexbox_content">
                        {/*  */}
                        <div className="radial-progress-container_box">
                            <div className="radial-progress_box">
                                <div className="radial-progress-container wanchenglv">
                                    <p></p>
                                    <div>
                                        <span>0</span>
                                        <em>%</em>
                                    </div>
                                </div>
                                <div className="radial-progress-container">
                                    <p></p>
                                    <div>
                                        <span>0</span>
                                        <em>%</em>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/*  */}
                        <div className="content-bar">
                           <div className="content-bar_box">
                                {
                                    barmain.map((item,index)=>{
                                        return(
                                            <div className="content-bar_small">
                                                <div className="content-bar_num">{item.num}</div>
                                                <div className="content-bar_xian"></div>
                                                <div className="content-bar_task">{item.task}</div>
                                            </div>
                                        )
                                    })
                                }
                           </div>
                        </div>
                    </div>
                    <div className="project-body-select" onClick={projectBodySelect}>
                        <input type="text" value={input_select} />
                        <span style={{display:project_body_select===0?'block':'none'}}>
                            <UpOutlined />
                        </span>
                        <span style={{display:project_body_select===1?'block':'none'}}>
                            <DownOutlined />
                        </span>
                        <div className="project-body-select_show"  style={{display:project_body_select===1?'block':'none'}}>
                            {
                                select.map((item,index)=>{
                                    return(
                                        <div onClick={()=>{statisticsSection(index)}}>
                                            <div>{item.title}</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
                <div className="statistics_section">
                    <h6><span></span></h6>
                    <div className="has-gutter_box">
                            <div className="header_wrapper_box">
                                {
                                    header_wrapper.map((item,index)=>{
                                        return(
                                            <div>
                                                <div className="header_text">{item.header}</div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <div className="zanwushuju"></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Statistics